
import React,{useEffect} from 'react';

function Notes() {
/* Variable Declaraions */
    let titles =[];
    let notes = [];
    let x= 0;
  
/* get local title strorage value */    
        let getTitle=localStorage.getItem('title');
        if(getTitle)
        {
            titles = JSON.parse(getTitle);
        }

/* get local notes strorage value */    
        let getNotes = localStorage.getItem('notes');
        if(getNotes)
        {
            notes = JSON.parse(getNotes);
        }

/* stroge the x value in local storage */    
        let i = localStorage.getItem('x')
        if(i)
        {
            x=JSON.parse(i);
            x++
        }
           
/* hide the input field */        
    useEffect(()=>
    {
        document.getElementById('displayHandler').style.visibility="hidden";
    },[])
    
/* visibility handler for when create new note */
    const displayHandler=()=>
    { 
        document.getElementById('displayHandler').style.visibility="visible";
            document.getElementById('delete').style.display="none";
                document.getElementById('title').value="";
                    document.getElementById('notes').value="";
    }


/* Save Titile & Notes in local storage,  When save the data */
    const saveData=()=>
    {
        if(document.getElementById('save').value === "")
        {

        }
        else 
        {
            x= document.getElementById('save').value;
        } 
        
        if(x<=6)
        {
            
            titles[x]=document.getElementById('title').value;
                notes[x]=document.getElementById('notes').value;
                if(titles == "" || notes == "")
                {
                    alert('Input field empty please enter the values')
                }
                else
                {
                    localStorage.setItem('title',JSON.stringify(titles));
                        localStorage.setItem('notes',JSON.stringify(notes));
                            localStorage.setItem('x',JSON.stringify(x)); 
                                x++;
                                window.location.reload(false);
                                document.getElementById("displayHandler").style.visibility="hidden";
                }
        }
               
               
    }


    const buttonHandler=(index)=>
    {
        document.getElementById('title').value = titles[index];
            document.getElementById('notes').value = notes[index];
                document.getElementById('displayHandler').style.visibility="visible";
                    titles[index]=document.getElementById('title').value;
                        document.getElementById('save').setAttribute('value',index);
                            document.getElementById('delete').setAttribute('value',index);
                                document.getElementById('delete').style.display="block";
    }
   
/* delete notes & title function */
    const deleteData=()=>
    {
       alert('Confirm to delete')
            var d=document.getElementById('delete').value;
                titles.splice(d,1);
                    notes.splice(d,1);
                        localStorage.setItem('title',JSON.stringify(titles));
                            localStorage.setItem('notes',JSON.stringify(notes));
                            localStorage.removeItem("x");
                            window.location.reload(false);
                            
    }
   
    return (
        <>  
            <header>
                <h1>Notes</h1>
            </header>

            <section>
                <nav>
                    <button type="button" id="btn" onClick={displayHandler}>Create New Notes</button>
                        <div id="buttonField">
                            {
                                titles.map((title,index) => <button type="button" key={index} id="addBtn" onClick={()=>buttonHandler(index)}>{title}</button>)
                            }
                        </div>  
                </nav>

                <article>
                   <div className="inputfield" id="displayHandler">
                        <input type="text" placeholder="Enter heading" name="title" id="title" />
                            <br></br>
                                <br></br>
                                    <textarea placeholder="Enter notes" name="notes" id="notes" ></textarea>
                                        <br></br>
                                            <button type="submit" id="save" onClick={()=>saveData()} >Save</button>
                                                <button type="submit" id="delete" onClick={()=> deleteData()}>Delete</button>
                                                    </div>        
                </article>
            </section>
        </>
    )
}

export default Notes
